button.MuiButtonBase-root:focus {
    outline: 0;
}

.inputLabels.MuiFormLabel-root{
    width: 11.5em; 
    text-align : left;
    margin:10px auto 0;
    color: rgba(0, 0, 0, 0.38);
    padding: 0;
    font-size: 1rem;
    font-family: "Roboto", "Helvetica", "Arial", sans-serif;
    font-weight: 400;
    line-height: 1;
    letter-spacing: 0.00938em;
    transform: translate(0, 1.5px) scale(0.75);
    transform-origin: top left;
}

.btn-submit.MuiButton-contained{
    margin-top: 3%;
    background-color:#555555 ;
    &.white-color{
        color:white;
        &:hover{
            background-color:#555555 ;
            color:white;
            }
        }
        &.grey-color{
        color:#000000;
        &:hover{
            background-color:#555555 ;
            color:#9f9f9f;
        }
    }
}

.btn-save.MuiButton-contained{
    margin-top: 3%;
    margin-bottom: 2%;
    background-color:#555555 ;
    &.white-color{
        color:white;
        &:hover{
            background-color:#555555 ;
            color:white;
            }
        }
        &.grey-color{
        color:#9f9f9f;
        &:hover{
            background-color:#555555 ;
            color:#9f9f9f;
        }
    }
}

.btn-edit.MuiIconButton-root{
    float: right;
    color:#555555;
    flex: 0 0 auto;
    padding: 12px;
    overflow: visible;
    font-size: 1.5rem;
    text-align: center;
    transition: background-color 150ms cubic-bezier(0.4, 0, 0.2, 1) 0ms;
    border-radius: 50%;
}

.field-select{
    width: 11.5em;
    text-align: left;
}

.labelColor{
    color: #555555;
}

.field-grid.MuiGrid-item{
    margin-top: 2%;
    box-sizing: border-box;
}

.my-profile {
    .MuiTabs-indicator {
        background-color: #555555;
    }
}

.table-performance {
    font-family: arial, sans-serif;
    border-collapse: collapse;
    width: 95%;
    margin: 2%;
}

.table-performance-td-th {
    border: 1px solid #dddddd;
    text-align: center;
}

.table-performance-tr:nth-child(even) {
    background-color: #dddddd;
}

.table-performance-input {
    width: -webkit-fill-available
}

@media (max-width: 767px) {
    .my-profile {
        .MuiFormControl-root {
            display: flex;
            // padding: 10px 20px;
            // margin-bottom: 10px;
        }

        .MuiGrid-root {
            padding: 10px 20px;
            margin-bottom: 10px;

            textarea {
                width: 100%;
            }
        }

        .MuiFormLabel-root {
            width: 100%;
            text-align: left;
        }

        .MuiFormLabel-root {
            // transform: translate(17px, 1.5px) scale(0.75);
            transform: translate(0, 1.5px) scale(0.75);
        }
        .field-select {
            width: 100%;
        }
        .MuiTabs-scroller .MuiTabs-flexContainer[role='tablist'] {
            justify-content: space-between;
        }
    }

}